@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&family=Oswald:wght@200..700&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --background: #f4f3e7;
  --primary: #2b2d42; /* for H1/H2, buttons, and links */
  --accent: #7c9885; /* muted green-gray for balance */
  --input-bg: #dfe3da; /* input background */
  --text-secondary: #dfe3da; /* optional body text */
  --text-sec: f5deb3;
  --sec-bg: #eeeddb;
}

body {
  background-color: #f4f3e7;
  color: #2b2d42;
}

.container {
  width: 95%;
  margin: 0 auto;
}

@media (max-width: 768px) {
  .container {
    width: 100%;
  }
}

header a {
  font-size: 3rem;
}

h1,
h2,
a,
span {
  font-family: "Oswald", sans-serif;
}

button {
  border: 4px solid var(--primary);
  color: var(--primary);
  padding: 5px 15px;
  font-family: "Oswald", sans-serif;
  border-radius: 7px;
  transition: 0.4ms all ease-in-out;
}

button:hover {
  background: var(--primary);
  color: var(--input-bg);
}

h3,
h4,
p {
  font-family: "Nunito Sans", sans-serif;
}

input:focus-visible {
  outline: none;
}

form label {
  text-transform: uppercase;
  font-weight: bold;
  color: var(--primary);
}

form input {
  padding: 5px 15px;
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--primary);
  background: #f4f3e7;
  border-radius: 0.5rem;
}

form select {
  padding: 5px 15px;
  font-weight: bold;
  background: #f4f3e7;
  color: var(--primary);
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"]::placeholder {
  text-transform: uppercase;
}

input[type="number"] {
  -moz-appearance: textfield;
  appearance: textfield;
}

input::placeholder {
  color: var(--primary);
}

.customer-info span {
  font-size: 1.2rem;
  text-transform: uppercase;
  padding: 5px 10px;
  border-radius: 14px;
  font-weight: bold;
  background: #f4f3e7;
}

.form-login {
  background: #d6d1c4;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
